<template>
<div class="fx-waiting">
    <div class="waitingBg bg"></div>
    <div class="title">{{waiting.resultTitle}}</div>
    <div class="content">{{waiting.resultText}}</div>
    <div class="btns">
        <div class="oneBtn blueBtn" v-if="result === 'true'" @click="waitingAction()">返回首页</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import { Indicator } from 'mint-ui';
export default {
    name:'waiting',
    data(){
        return{
            result:null,
            waiting:{
                resultTitle:"提交成功",
                resultText:"您的相关资料已提交人工审核，人工审核大概需要 1-3个工作日，请您耐心等待！"
            }
        }
    },
    created(){

    },
    methods:{
        waitingAction(){
            window.location.href = this.$common.api_root+"/m/index.html#/personal"
        }
    },
    mounted(){
        let orderId = this.$route.query.orderId;
        Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
        });
        this.$http.getHttp({
                name:'fxJudgeBC',
                params:{
                    orderId:orderId
                },
                type:'get'
            },
            res => {
                Indicator.close();
                console.log(res);
                this.result = res.data;
            })
    }
}
</script>

<style lang="scss">
.fx-waiting{
    width:100%;
    height:100%;
    background:#fff;
    position: relative;
    overflow: hidden;
    .waitingBg{
        background: url("../../../static/images/waiting.png") no-repeat;
    }
    .bg{
        width:r(519);
        height:r(490);
        background-size:100% 100%;
        margin:r(120) auto 0;
    }
        .title{
        font-size:r(34);
        color:#353535;
        margin-top:r(-129);
        text-align: center;
        margin-bottom:r(10);
    }
    .content{
        line-height:r(46);
        padding:0 r(80);
        color:#666;
        font-size:r(26);
    }
    .btns{
        padding:r(80) r(30) 0 r(30);
        .oneBtn{
            height:r(88);
            line-height:r(88);
            font-size:r(32);
            text-align: center;
            border-radius:r(10);
            margin-bottom:r(40);
        }
        .blueBtn{
            background:#0084ff;
            color:#fff;
        }
    }
}

</style>


